<template>
    <div class="detail" >
            <div>登录成功!!!!</div>
            <input type="button" value="退出当前账号" @click="exit">
      </div>
</template>

<script>
export default {
  name: 'lingdai',
  methods: {
    exit () {
      console.log('nihao')
      window.localStorage.removeItem('userinfo')
      window.localStorage.removeItem('l-cart')
      this.$router.history.push('/home')
    }
  }
}
</script>

<style lang="scss" scoped>
.detail{
    text-indent: 2em;
    font-size: 15px;
    height: 50px ;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
</style>



